@import '../../common/styles/sass/variable';
@import '../../common/styles/sass/mixins';
@import '../../common/components/Button/Button.module';


.form {

  input[type="text"],
  input[type="email"],
  textarea,
  select {
    display: block;
    background-clip: padding-box;
    box-sizing: border-box;
    background: transparent;
    outline: none;
    border: 1px solid $borderColor;
    color: $textColor;
    padding: .9em 1em;
    font-size: 1em;
    font-family: open sans, sans-serif;
    width: 100%;
    margin-bottom: 30px;
  }

  .formRow {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .col {
      position: relative;
      flex: 0 0 50%;
      max-width: 50%;
    }

  }

  .itemFrom {
    background: transparent;
    outline: none;
    border: 1px solid $borderColor;
    color: $textColor;
    padding: .9em 1em;
    font-size: 1em;
    font-family: open sans, sans-serif;
    width: 100%;
  }

  .textarea {
    resize: vertical;
    min-height: 160px;
  }

  .formGroup {
    position: relative;

    .labelFrom {
      position: absolute;
      color: #F0F0F0;
      top: 11px;
      left: 7px;
      padding: 0 14px;
      opacity: .6;
      pointer-events: none;
      transition: 0.3s;
    }

    .itemFrom:valid ~ .labelFrom {
      top: -13px;
      left: 10px;
      padding: 0 9px;
      background: #262b2f;
      transition: 0.3s;
    }

    .itemFrom:focus + .labelFrom,
    .itemFrom:active ~ .labelFrom {
      font-weight: bold;
      color: $primaryColor;
      top: -13px;
      left: 10px;
      padding: 0 9px;
      background: #262b2f;
      transition: 0.3s;
      opacity: 1;
    }




  }

  .formGroup:nth-child(1) {
    input {
      width: 95%;
    }
  }

  .wrapBtnSub {
    display: flex;
    justify-content: flex-end;
  }

}

button[type='submit'] {
  @extend .btnOutline;
  align-self: flex-end;
  font-family: open sans, sans-serif;
  font-size: 16px;
  border: 2px solid $primaryColorDark;

  &:disabled {
    color: $borderColor;
    border: 2px solid $borderColor;

    &:hover {
      background: transparent;
      cursor: auto;
    }
  }
}

