@import '../common/styles/sass/variable';

.headerBlock {
  position: fixed;
  width: 100%;
  z-index: 3;

  .header {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 80px;
    margin: 0 auto;
    width: 50%;
  }

}

